<template lang="pug">
div.container(class="pt-5")
  div#screen
    ul#grid(
      ref="grid"
      :class="{'fixed':isFull, 'gridLine':grid.isGridLine}"
      :style="ulStyle"
    )
      li(
        v-for="(c,index) in grid.blocks"
        :style="liStyle"
      )
        span(:class="{'d-none':!grid.isGridIndex}") {{ index+1 }}
        div.content

  .toolBtns
    .fullBtn(@click="isFull = !isFull")
      i(class="fas fa-external-link-alt flip-h")
</template>

<script>
export default {
  watch: {
    'screenHeight' (newHeight) {
      this.screenHeight = newHeight
    },
    'grid.row' (newRow) {
      if ((newRow > 0) && (newRow <= 16)) {
        this.grid.blocks = this.getBlocks
        this.grid.liHeight = this.getliHeight
      }
    },
    'grid.column' (newColumn) {
      if ((newColumn > 0) && (newColumn <= 18)) {
        this.grid.blocks = this.getBlocks
        this.grid.liWidth = this.getliWidth
      }
    },
    'grid.blocks' (newBlocks) {
      this.grid.blocks = newBlocks
    },
    'grid.liWidth' (newLiWidth) {
      this.grid.liWidth = newLiWidth
    },
    'grid.liHeight' (newLiHeight) {
      this.grid.liHeight = newLiHeight
    }
  },
  data () {
    return {
      isFull: false,
      ratio: window.screen.availWidth / document.documentElement.clientHeight,
      screenHeight: document.body.clientHeight,
      gridWidth: 0,
      gridHeight: 0,
      grid: {},
      displayGridGutter: 0
    }
  },
  computed: {
    getBlocks () {
      return this.grid.row * this.grid.column
    },
    getliWidth () {
      return (100 / this.grid.column) + '%'
    },
    getliHeight () {
      return (100 / this.grid.row) + '%'
    },
    getGutter () {
      // min-gutter
      if (this.gridWidth * 1.5 < 992) {
        return this.grid.gutter * ((992 / 1.5) / screen.width)
      } else {
        return this.grid.gutter * (this.gridWidth / screen.width)
      }
    },
    ulStyle () {
      if (this.isFull) { // fullscreen
        return {
          width: screen.width + 'px',
          height: this.screenHeight + 'px',
          top: 0,
          left: 0
        }
      } else {
        return {
          minWidth: (992 / 1.5) + 'px',
          minHeight: (992 / 1.5 / this.ratio) + 'px',
          width: this.gridWidth + 'px',
          height: this.gridHeight + 'px'
        }
      }
    },
    liStyle () {
      if (this.isFull) { // fullscreen
        return {
          flex: this.grid.liWidth,
          height: this.grid.liHeight,
          paddingLeft: this.grid.gutter + 'px',
          paddingRight: this.grid.gutter + 'px'
        }
      } else {
        return {
          flex: this.grid.liWidth,
          height: this.grid.liHeight,
          paddingLeft: this.getGutter + 'px',
          paddingRight: this.getGutter + 'px'
        }
      }
    }
  },
  mounted () {
    const vm = this
    vm.$bus.$on('initGrid', (value) => {
      vm.grid = value
      vm.displayGridGutter = vm.getGutter
    })

    console.log(window.innerHeight)

    this.gridWidth = window.innerWidth / 1.5
    this.gridHeight = window.innerWidth / 1.5 / this.ratio

    window.onresize = () => {
      return (() => {
        this.screenHeight = document.body.clientHeight
        this.gridWidth = window.innerWidth / 1.5
        this.gridHeight = window.innerWidth / 1.5 / this.ratio
        this.displayGridGutter = this.getGutter
      })()
    }
  }
}
</script>



