<template lang="pug">
  div(class="m-4 mb-5")
    div
      .type row:
      input#gridRow(
        type="number" min="1" max="16"
        class="form-control" style="width: 5rem;"
        :value="grid.row" @input="grid.row = parseInt($event.target.value)"
      )
    div
      .type column:
      input#gridHeight(
        type="number" min="1" max="18"
        class="form-control" style="width: 5rem;"
        :value="grid.column" @input="grid.column = parseInt($event.target.value)"
      )
    div
      .type gutter:
      input#gridGutter(
        type="number" min="0" max="35"
        class="form-control" style="width: 5rem;"
        :value="grid.gutter" @input="grid.gutter = parseInt($event.target.value)"
      )
    label(class="switch")
      input#gridLine(
        type="checkbox"
        v-model="grid.isGridLine"
      )
      span gridLine
    label(class="switch")
      input#gridIndex(
        type="checkbox"
        v-model="grid.isGridIndex"
      )
      span gridIndex
</template>

<script>
export default {
  data () {
    return {
      grid: {
        row: 6,
        column: 12,
        blocks: 0,
        liWidth: 0,
        liHeight: 0,
        gutter: 15,
        isGridLine: true,
        isGridIndex: true
      }
    }
  },
  computed: {
    getBlocks () {
      return this.grid.row * this.grid.column
    },
    getliWidth () {
      return (100 / this.grid.column) + '%'
    },
    getliHeight () {
      return (100 / this.grid.row) + '%'
    }
  },
  created () {
    this.grid.liWidth = this.getliWidth
    this.grid.liHeight = this.getliHeight
    this.grid.blocks = this.getBlocks

    setTimeout(() => {
      this.$bus.$emit('initGrid', this.grid)
    }, 10)
  }
}
</script>

<style scope lang="scss">
</style>



